	/**
	 * Get file name of the exported excel file
	 */
	getExcelFileName() {
		return 'الملخص المالي' + '-' + new Date().toISOString().slice(0, 10);
	}

	downloadPdf() {
		let dateFrom = new Date(+new Date(this.dateFrom) - this.tzoffset).toISOString().slice(0, 16);
		let dateTo = new Date(+new Date(this.dateTo) - this.tzoffset).toISOString().slice(0, 16);
		let url = '?From=' + dateFrom + '&To=' + dateTo;
		let headers = this.headers;
		headers.set('Accept', 'application/pdf');

		this.http.get(
			globalVariables.baseUrl + 'api/Reports/Financial/Summary/pdf' + url,
			{headers, responseType: 'blob'}
		).subscribe(res => {
				saveAs(res, `pdf report.pdf`)
				console.log('ok');
			}, error1 => {
				console.log(error1.error)
			}
		);
	}

	public downloadAsPDF() {

		console.log(this.dataSource.data);
		const doc = new jsPDF();

		const myFont = globalVariables.pdfFont;
		console.log(myFont);

		var callAddFont = function () {
			doc.addFileToVFS("MyFont.ttf", myFont);
			doc.addFont("MyFont.ttf", "MyFont", "normal");
		};
		jsPDF.API.events.push(['addFonts', callAddFont]);

		doc.addFileToVFS("MyFont.ttf", myFont);
		doc.addFont("MyFont.ttf", "MyFont", "normal");
		doc.setFont("MyFont");


		const columns = [['الشهر', 'مجموع النقد الكلي']];


		let data = [];

		for (let report of this.dataSource.data) {
			data.push([report.date, report.totalCashBalance]);
		}

		autoTable(doc,
			{
				head: columns,
				headStyles: {
					halign: "center"
				},
				columnStyles: {
					0: {
						halign: 'center',
						textColor: [0, 0, 0]

					},
					1: {
						halign: 'center',
						textColor: [0, 0, 0]
					}
				},
				body: data,
				styles: {
					font: 'MyFont',    // <-- place name of your font here
					fontStyle: 'bold',
					fontSize: 12,
					// fillColor: [0, 0, 0]
				},
				margin: {top: 40},
				didDrawPage: (dataArg) => {
					let fromDate = new Date(+new Date(this.dateFrom) - this.tzoffset);
					let toDate = new Date(+new Date(this.dateTo) - this.tzoffset);
					var imgData = 'assets/media/logos/funny-gas-jockey.jpg';
					doc.addImage(imgData, 'JPEG', dataArg.settings.margin.left, 4, 20, 20);
					doc.text('تقرير الملخص المالي', 95, 10);
					doc.text(`من شهر: ${fromDate.getFullYear()}/${fromDate.getMonth() + 1}`, 165, 12);
					doc.text(`الى شهر: ${toDate.getFullYear()}/${toDate.getMonth() + 1}`, 165, 20);

					doc.line(0, 25, 300, 25);
				}
			});

		doc.save('table.pdf');


	}


